<template>
  <div class="Wokoo" v-if="show">
    <header class="Wokoo-header">
      <img :src="logo" class="Wokoo-logo" alt="logo" />
      <span class="Wokoo-close-icon" @click="handleClose">
        ×
      </span>
      <p>Edit <code>App.vue</code> and save to reload.</p>
      <a
        class="Wokoo-link"
        href="https://juejin.cn/post/6922815205575491597"
        target="_blank"
        rel="noopener noreferrer"
      >
        Learn Wokoo000000000
      </a>
      <el-button type="primary">1123</el-button>
    </header>
  </div>
  <div class="Wokoo-hide" @click="handleClose" v-else>
    <img :src="logo" class="Wokoo-hide-logo" alt="logo" />
    open
  </div>
</template>
<script>
import "./app.less";
import logo from "../public/icon.jpg";
import { proxy } from "ajax-hook";
export default {
  data: function() {
    return {
      logo,
      show: true,
    };
  },
  methods: {
    handleClose() {
      this.show = !this.show;
    },
  },
  mounted() {
    console.log(proxy);
    proxy({
      //请求发起前进入
      onRequest: (config, handler) => {
        console.log(config);
        handler.next(config);
      },
      //请求发生错误时进入，比如超时；注意，不包括http状态码错误，如404仍然会认为请求成功
      onError: (err, handler) => {
        console.log(err.type);
        handler.next(err);
      },
      //请求成功后进入
      onResponse: (response, handler) => {
        handler.next(response);
      },
    });
  },
};
</script>
<style scoped></style>
